<template>
  <div class="investigation-and-evidence-box" v-show="investigationAndEvidenceBoxDispFLg" :class="[downFlg ? 'downAnimate' : 'upAnimate']">
    <div>
      <div class="item-box">
        <span class="third-title cl"><span v-if="historyItem"></span></span>
        <div class="upload-box cl">
          <!-- <div class="fl"></div> -->
          <div id="handDrawn" :class="[clickHandDrawnFlg ? 'clicked' : '']" @click="freeHand()">
            {{ freeHandTxt }}
          </div>
          <div id="track" :class="[trackFlg ? 'clicked' : '']" @click="track()">
            {{ trackTxt }}
          </div>
        </div>
        
      </div>
      <div class="item-box">
        <span class="third-title cl"></span>
        <select name="" id="superviseType">
          <option value="88"></option>
          <option value="0"></option>
          <option value="1"></option>
          <option value="2"></option>
        </select>
      </div>
      <div class="item-box take-photo-txt cl">
        <span class="third-title cl">/</span>
        <div v-for="(item, index) in thumbnailList" v-show="thumbnailList.length > 0">
          <img :src="item" width="100%" height="100%" />
          <div class="close-16-ico delete-img-btn" @click="deleteImg($event, index)"></div>
        </div>
        <div class="take-photo-btn" @click="takePhoto()">
          <span class="camera-gray-big-ico"></span>
        </div>
      </div>
      <div class="item-box">
        <span class="third-title cl">&nbsp;&nbsp;</span>
        <textarea id="superViseDesc" class="describ" placeholder=""></textarea>
      </div>
      <div class="tl mt15 mb15 cl" @click="anonymousSubmit()">
        <span :class="[anonymousCheckFlg ? 'check-16-cur-ico' : 'check-16-ico']" class="check-ico fl"></span>
        <span class="anonymous-submit fl"></span>
      </div>
      <div class="upload-btn" @click="upload()" v-if="!historyItem">
        <span class="upload-ico"></span>
       {{isOffline? '&nbsp;&nbsp;':'&nbsp;&nbsp;'}} 
      </div>
      <div class="upload-btn" @click="update()" v-if="historyItem">
        <span class="upload-ico"></span>
        &nbsp;&nbsp;
      </div>
        <!-- <div class="upload-btn" @click="uploadOrSave('save')" v-if="isOffline">
        <span class="upload-ico"></span>
        &nbsp;&nbsp;
      </div> -->
    </div>
    <a href="javascript:void(0)" class="close-menu-btn" @click="closeMenuBtnCheck()">
      <span class="close-16-ico"></span>
    </a>
    <loading :parentHandler="this" ref="loading"></loading>
  </div>
</template>

<script>
import axios from "axios";
import { hostUrl, artMsg, CoordinateUtil } from "../common/common";
import loading from "../common/loading";

import { Vector as SourceVec } from "ol/source";
import VectorLayer from "ol/layer/Vector";
import Feature from "ol/Feature";
import LineString from "ol/geom/LineString";
import Point from "ol/geom/Point";
import { Polygon } from "ol/geom";
import Style from "ol/style/Style";
import Stroke from "ol/style/Stroke";
import { asArray } from "ol/color";
import bus from "../common/bus.js";
export default {
  components: {
    loading,
  },
  name: "investigationAndEvidence",
  props: ["parentHandler","isOffline"],
  data() {
    return {
      investigationAndEvidenceBoxDispFLg: false,
      clickHandDrawnFlg: false,
      trackFlg: false,
      photoList: [],
      thumbnailList: [],
      downFlg: false,
      anonymousCheckFlg: false,
      freeHandTxt: "",
      trackTxt: "",
      intervalT: null,
      lonlats: [],
      historyItem: null
    };
  },
  mounted() {
    bus.$on("clickHistoryItemEvent", (res) => {
      this.investigationAndEvidenceBoxDispFLg = true;
      this.historyItem = res;
      document.getElementById("superviseType").value = res.superviseType.id;
      document.getElementById("superViseDesc").value = res.superviseDesc
        ? res.superviseDesc
        : "";
      this.anonymousCheckFlg = res.isAnonymous ? true : false;
      this.thumbnailList = res.imageUrl;
      this.photoList = res.imageUrl;
    });
  },
  methods: {
    // 
    anonymousSubmit() {
      this.anonymousCheckFlg = !this.anonymousCheckFlg;
    },
    // 
    deleteImg(e, imgIndex) {
      this.photoList = this.photoList.filter((item, index) => index !== imgIndex);
      this.thumbnailList = this.thumbnailList.filter((item, index) => index !== imgIndex);
    },
    //   
    dispAllMenus() {
      this.investigationAndEvidenceBoxDispFLg = true;
      if (localStorage.cmjdData && !this.historyItem) { // 
        const cmjdData = JSON.parse(localStorage.cmjdData)
        document.getElementById("superviseType").value = cmjdData.superviseType;
        document.getElementById("superViseDesc").value = cmjdData.superviseDesc;
        const superviseInfo = cmjdData.superviseInfo ? JSON.parse(cmjdData.superviseInfo) : null;
        this.$parent.handDraws = superviseInfo;
        if (superviseInfo && superviseInfo.length > 0) {
          superviseInfo.forEach((polygonArr) => {
            // 
            const myPolygon = new Polygon(polygonArr); //
            const feature = new Feature(myPolygon);
            this.$parent.source.addFeature(feature);
          });
        }

        this.anonymousCheckFlg = cmjdData.isAnonymous ? true : false;
        this.thumbnailList = cmjdData.imageList;
        this.thumbnailList.forEach(item => { //path  base64
          this.imgTransBase64(item);
        })
      //  this.photoList = cmjdData.imageList;
      }
    },
    hideAllMenus() {
      this.investigationAndEvidenceBoxDispFLg = false;
    },
    // 
    update() {
      var _this = this;
      var postUrl = hostUrl + "supervise/modify";
      var superviseType = document.getElementById("superviseType").value;
      var superViseDesc = document.getElementById("superViseDesc").value;
      superViseDesc = superViseDesc.trim();
      var isAnonymous = _this.anonymousCheckFlg ? 1 : 0;
      var superviseInfo =
        _this.$parent.handDraws.length > 0
          ? JSON.stringify(_this.$parent.handDraws)
          : "";
      const data = {
        historyId: _this.historyItem.historyId,
        imageList: _this.photoList,
        isAnonymous: isAnonymous,
        superviseDesc: superViseDesc,
        superviseInfo: superviseInfo,
        superviseType: superviseType,
        timestamp: "",
      };
      if (!data.superviseInfo) {
        artMsg("");
        return;
      }
      if (data.superviseType == "88") {
        artMsg("");
        return;
      }
      if (data.imageList.length == 0) {
        artMsg("");
        return;
      }
      _this.$refs.loading.modelShow();
      axios({
        method: "post",
        url: postUrl,
        data: data,
        headers: {
          token: localStorage.token,
        },
      })
        .then(function (response) {
          _this.$refs.loading.modelHide();
          if (response.data.status) {
            _this.closeMenuBtn();
            artMsg("");
            this.$parent.historyManage(); //
          } else {
            artMsg(response.data.message);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    // 
    getConnectionType() {
      if (api.connectionType == "none") {
        return true;
      } else {
        return false
      }
    },
    // 
    upload() {
      var _this = this;
      var postUrl = hostUrl + "supervise/submit";

      var superviseType = document.getElementById("superviseType").value;
      var superViseDesc = document.getElementById("superViseDesc").value;
      superViseDesc = superViseDesc.trim();
      var isAnonymous = _this.anonymousCheckFlg ? 1 : 0;
      // var superviseInfo = _this.$parent.coordinatesArr.length > 0 ? JSON.stringify(_this.$parent.coordinatesArr) : ''
      var superviseInfo =
        _this.$parent.handDraws && _this.$parent.handDraws.length > 0
          ? JSON.stringify(_this.$parent.handDraws)

          : "";
      const data = {
        imageList: _this.photoList,
        isAnonymous: isAnonymous,
        superviseDesc: superViseDesc,
        superviseInfo: superviseInfo,
        superviseType: superviseType,
        timestamp: "",
      };
      if (!data.superviseInfo) {
        artMsg("");
        return;
      }
      if (data.superviseType == "88") {
        artMsg("");
        return;
      }
      if (data.imageList.length == 0) {
        artMsg("");
        return;
      }
      if (data.imageList.length > 3) {
        artMsg("");
        return;
      }

      const isOffline = this.getConnectionType();
      if (isOffline) { // 
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          customClass: 'message-box',
          showClose: false
        }).then(() => {
          localStorage.setItem("cmjdData", JSON.stringify(data))
          //
          this.closeMenuBtn();
        }).catch(() => {
          localStorage.removeItem("cmjdData")
        });
        return
      }

      _this.$refs.loading.modelShow();
      axios({
        method: "post",
        url: postUrl,
        data: data,
        headers: {
          token: localStorage.token,
        },
      })
        .then(function (response) {
          _this.$refs.loading.modelHide();
          if (response.data.status) {
            console.log(response);
            _this.closeMenuBtn();
             localStorage.removeItem("cmjdData")
            artMsg("");
          } else {
            artMsg(response.data.message);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    // base64
    imgTransBase64(imgPath) {
      var _this = this;
      _this.Trans.decodeImgToBase64(
        {
          imgPath: imgPath,
        },
        function (ret, err) {
          if (ret.status) {
            var tBase64Str = "data:image/jpg;base64," + ret.base64Str;
            // _this.thumbnailList = _this.thumbnailList.concat(imgPath)
            _this.photoList = _this.photoList.concat(tBase64Str);
            //   _this.dataURLtoBlob(tBase64Str)
          } else {
            artMsg(JSON.stringify(err));
          }
        }
      );
    },
    // 
    takePhoto() {
      if (this.photoList.length >= 3) {
        artMsg("");
        return false;
      }
      // 
      const photos = api.hasPermission({ list: ['camera', 'photos', 'photos-w', 'storage'] })
      const result = photos.every(function (v) {
        console.log(JSON.stringify(v))
        return v.granted === true
      })
      if (result) {
        let _this = this;
        api.actionSheet({
          title: '',
          cancelTitle: '',
          buttons: ['', '']
        }, function (ret, err) {
          console.log(ret.buttonIndex)
          if (ret.buttonIndex !== 3) { // 3
            let sourceType = (ret.buttonIndex == 1 ? 'camera' : 'album');
            getPicture(sourceType);
          }
        });
        //
        function getPicture(sourceType) {
          api.getPicture({
            sourceType: sourceType,
            encodingType: 'jpg',
            mediaValue: 'pic',
            destinationType: 'base64',
            allowEdit: true,
            quality: 50,
            saveToPhotoAlbum: true
          }, function (ret, err) {
            if (ret) {
              if (ret.data && ret.base64Data) {
                _this.thumbnailList = _this.thumbnailList.concat(ret.data)
                _this.photoList = _this.photoList.concat(ret.base64Data);
              }
            } else {
              api.alert({
                msg: JSON.stringify(err)
              });
            }
          });
        }
      } else {
        const permission = ['camera', 'photos', 'photos-w', 'storage'];
        api.confirm({
          msg: '',
          buttons: ['', '']
        }, function (ret) {
          if (ret.buttonIndex == 2) {
            api.requestPermission({
              list: permission,
            }, function (res) {
              const item = res.list.every(function (v) {
                return v.granted === true
              })
              if (item) {
                // 
                api.alert({ msg: '' });
              }
            });
          }
        });
      }
      // // 
      // var _this = this;
      // _this.FNPhotograph.open(
      //   {
      //     path: "fs://savePath",
      //     album: true,
      //     quality: "medium",
      //     usePreview: true,
      //   },
      //   function (ret) {
      //      alert(JSON.stringify(ret));
      //     if (!!ret.imagePath && _this.photoList.length < 3) {
      //       _this.imgTransBase64(ret.imagePath);
      //       // alert(ret.imagePath)
      //     } else if (_this.photoList.length >= 3) {
      //       artMsg("");
      //       return false;
      //     }
      //   }
      // );
    },
    // 
    openPhoto() {
      Api.getPicture(
        {
          sourceType: "camera",
          encodingType: "jpg",
          mediaValue: "pic",
          destinationType: "base64",
          allowEdit: true,
          quality: 50,
          targetWidth: 70,
          targetHeight: 70,
          saveToPhotoAlbum: false,
        },
        function (ret, err) {
          if (ret) {
            artMsg(JSON.stringify(ret));
          } else {
            artMsg(JSON.stringify(err));
          }
        }
      );
    },
    // click 
    freeHand() {
      if (!this.clickHandDrawnFlg) {
        this.clickHandDrawnFlg = true;
        this.trackFlg = false;
        this.downFlg = true;
        this.freeHandTxt = "";
        // this.$parent.clearFreeHandDraw();Polygon LineString
        this.$parent.freeHandDraw("Polygon");
      } else {
        this.clickHandDrawnFlg = false;
        this.trackFlg = false;
        this.downFlg = false;
        this.freeHandTxt = "";
        this.$parent.stopFreeHandDraw();
        this.$parent.clearFreeHandDraw();
      }
    },
    // 
    getPosition(timer) {
      var _this = this;
      _this.intervalT = setInterval(function () {
        _this.$parent.BMap.getLocation(
          {
            accuracy: "10m",
            autoStop: true,
            filter: 1,
            enableLocInForeground: true,
            notification: {
              id: 1,
              contentTitle: "",
              contentText: "",
            },
          },
          function (ret, err) {
            if (ret.status) {
              //  artMsg("");
              var lat = ret.lat;
              var lon = ret.lon;
              var tmpObj = CoordinateUtil.getWgs84xy(lon, lat);
              var latlotArr = [tmpObj.lng, tmpObj.lat];
              _this.lonlats.push(latlotArr);
              console.info("latLots:" + _this.lonlats);
              // _this.$parent.addLine(_this.lonlats);
            } else {
              //   artMsg(err.code);
            }
          }
        );
      }, timer);
    },
    // 
    track() {
      var _this = this;
      var timer = 10000; // 
      if (!_this.trackFlg) {
        _this.clickHandDrawnFlg = false;
        _this.trackFlg = true;
        _this.downFlg = true;
        _this.trackTxt = "";
        _this.getPosition(timer);
        // _this.$parent.continuousPositioning();
      } else {
        clearInterval(_this.intervalT);
        _this.$parent.BMap.stopLocation();
        this.clickHandDrawnFlg = false;
        this.trackFlg = false;
        this.downFlg = false;
        this.trackTxt = "";
        console.info("latLots:" + this.lonlats);
        this.addLineString(this.lonlats);
      }
    },
    // 
    addLineString(lnglats, params) {
      // var lnglats = [[123.42433249026216,41.81222047454243],[127.42387980653746,45.81173388415827]];
      if (!params) {
        params = {};
      }
      if (!params["color"]) {
        params["color"] = "#ff0309";
      }
      if (!params["width"]) {
        params["width"] = 3;
      }
      // 
      let feature = new Feature({
        geometry: new LineString(lnglats),
        name: "Line",
      });
      // 
      let sourceVec = new SourceVec({
        features: [feature],
      });
      // 
      let vercorLayer = new VectorLayer({
        source: sourceVec,
        style: new Style({
          stroke: new Stroke({
            width: params.width,
            color: asArray(params.color),
          }),
        }),
      });
      //  return vercorLayer
      this.$parent.map.addLayer(vercorLayer);
    },

    closeMenuBtnCheck() {
       this.closeMenuBtn();
      // if (this.historyItem) {
      //   this.closeMenuBtn();
      //   return
      // }
      // const superviseType = document.getElementById("superviseType").value;
      // let superViseDesc = document.getElementById("superViseDesc").value;
      // superViseDesc = superViseDesc.trim();
      // const isAnonymous = this.anonymousCheckFlg ? 1 : 0;
      // const superviseInfo =
      //   this.$parent.handDraws && this.$parent.handDraws.length > 0
      //     ? JSON.stringify(this.$parent.handDraws)
      //     : "";
      // const data = {
      //   imageList: this.thumbnailList,
      //   isAnonymous: isAnonymous,
      //   superviseDesc: superViseDesc,
      //   superviseInfo: superviseInfo,
      //   superviseType: superviseType,
      //   timestamp: "",
      // };
      // if (superviseType !== "88" || superViseDesc || isAnonymous || superviseInfo || this.thumbnailList.length > 0) {
      //   this.$confirm('?', '', {
      //     confirmButtonText: '',
      //     cancelButtonText: '',
      //     type: 'warning',
      //     customClass: 'message-box',
      //     showClose: false
      //   }).then(() => {
      //     localStorage.setItem("cmjdData", JSON.stringify(data))
      //     //
      //     this.closeMenuBtn();
      //   }).catch(() => {
      //     localStorage.removeItem("cmjdData")
      //     this.closeMenuBtn();
      //   })
      // } else {
      //   this.closeMenuBtn();
      // }
    },

    // 
    closeMenuBtn() {
      this.closeMe();
      this.$parent.clearSecondMenu();
      this.$parent.dispCtrlBox();
      this.$parent.dispMapZoom();
      this.$parent.takePhotoUploadFlg = false;
      if (this.historyItem) {
        //
        this.$parent.historyManage();
      }
      this.clearAllForm();
    },
    // 
    closeMe() {
      this.investigationAndEvidenceBoxDispFLg = false;
    },
    // 
    clearAllForm() {
      this.clickHandDrawnFlg = false;
      this.trackFlg = false;
      this.downFlg = false;
      this.freeHandTxt = "";
      this.trackTxt = "";
      document.getElementById("superviseType").value = "88";
      this.$parent.handDraws = "";
      document.getElementById("superViseDesc").value = "";
      this.anonymousCheckFlg = false;
      this.photoList = [];
      this.thumbnailList = [];
      this.historyItem = "";
      this.$parent.source.clear(); //
      this.$parent.stopFreeHandDraw(); //
    },
  },
};
</script>
<style src="@/assets/css/upload.css"></style>
<style>
.message-box {
  width: auto !important;
}
</style>



